<template>
  <div class="PayCheck">
    <div class="header">
      <div style="float:left;margin-top: -5px;">
        <span style="font-size:16px;">当前位置:</span>
      </div>
      <div style="padding-left: 80px;">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item>转账业务</el-breadcrumb-item>
          <el-breadcrumb-item>转账制单</el-breadcrumb-item>
          <el-breadcrumb-item>单笔付款</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
    </div>
    <div>
      <div v-if="active === 1" class="position-relative">
        <div class="w-100p mt-20" style="max-height: 500px;overflow-y: auto;border: solid 1px #8DB6DA">
          <el-table :data="payTransferInfo" highlight-current-row fit size="mini">
            <el-table-column label="通过" align="center" width="50">
              <template slot-scope="{ row }">
                <el-radio v-model="row.pass" :label="1" @change="checkColumn(row)"><span></span></el-radio>
              </template>
            </el-table-column>
            <el-table-column label="不通过" align="center" width="50">
              <template slot-scope="{ row }">
                <el-radio v-model="row.pass" :label="0" @change="checkColumn(row)"><span></span></el-radio>
              </template>
            </el-table-column>
            <el-table-column label="凭证号" prop="number" align="center">
              <template>10082*******</template>
            </el-table-column>
            <el-table-column label="户名" prop="number" align="center">
              <template>{{ companyInfo.name }}</template>
            </el-table-column>
            <el-table-column label="付款人账号" prop="payAccountNum" align="center">
              <template>{{ companyInfo.number }}</template>
            </el-table-column>
            <el-table-column label="户名" prop="payAccountNum" align="center">
              <template slot-scope="{ row }">
                {{ row.payeeAccountName }}
              </template>
            </el-table-column>
            <el-table-column label="收款人账号" prop="payeeAccountNum" align="center">
              <template slot-scope="{ row }">
                {{ row.payeeAccountNum }}
              </template>
            </el-table-column>
            <el-table-column label="开户行" prop="payeeAccountNum" align="center">
              <template slot-scope="{ row }">
                {{ row.payeeBank }}
              </template>
            </el-table-column>
            <el-table-column label="定条件" prop="payeeAccountNum" align="center">
              <template>定金额</template>
            </el-table-column>
            <el-table-column label="金额（元）" align="center">
              <template slot-scope="{ row }">
                {{ row.money }}
              </template>
            </el-table-column>
            <el-table-column label="用途" align="center">
              <template slot-scope="{ row }">
                {{ row.toUse }}
              </template>
            </el-table-column>
            <el-table-column label="定时定频" prop="payeeAccountNum" align="center">
              <template>实时</template>
            </el-table-column>
            <el-table-column label="交易类型" prop="payeeAccountNum" align="center">
              <template>单笔付款</template>
            </el-table-column>
          </el-table>
          <div v-if="!pass" style="margin: 10px 20px">复核不通过原因：<el-input v-model="reason" style="width: 300px;" @input="inputChange"></el-input></div>
        </div>
        <div class="bottom w-100p">
          <div>总笔数：</div>
          <div class="mr-20">{{ totalNum }} 笔</div>
          <div>总金额（元）：</div>
          <div class="mr-20">{{ totalMoney.toFixed(2) }}</div>
          <div>复核通过总笔数：</div>
          <div class="mr-20">{{ passNum }} 笔</div>
          <div>复核通过总金额（元）：</div>
          <div class="mr-20">{{ passMoney.toFixed(2) }}</div>
          <div>复核不通过总笔数：</div>
          <div class="mr-20">{{ noPassNum }} 笔</div>
          <div>复核不通过总金额（元）：</div>
          <div>{{ noPassMoney.toFixed(2) }}</div>
        </div>
        <div class="text-center mt-20 mb-20">
          <el-button type="primary" size="medium" @click="check">下一步</el-button>
        </div>
      </div>
      <div v-if="active === 2">
        <table class="reviewContent-table">
          <tbody>
          <tr>
            <td rowspan="3" width="80" class="rightBottomLine" style="background-color: #f7fbfe;"><p style="width: 20px;margin-left: 31px;">付款人</p></td>
            <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;">户名：</td>
            <td class="rightBottomLine" style="text-align: left;padding-left: 15px;">{{ companyInfo.name }}</td>
            <td rowspan="3" width="80" class="rightBottomLine" style="background-color: #f7fbfe;"><p style="width: 20px;margin-left: 31px;">收款人</p></td>
            <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;">户名：</td>
            <td style="text-align: left;padding-left: 15px;">{{ checkInfo.payeeAccountName }}</td>
          </tr>
          <tr>
            <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;">账号：</td>
            <td class="rightBottomLine" style="text-align: left;padding-left: 15px;">{{ companyInfo.number }}</td>
            <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;">账号：</td>
            <td style="text-align: left;padding-left: 15px;">{{ checkInfo.payeeAccountNum }}</td>
          </tr>
          <tr>
            <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;">开户行：</td>
            <td class="rightBottomLine" style="text-align: left;padding-left: 15px;">{{ companyInfo.bank }}</td>
            <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;">开户行：</td>
            <td style="text-align: left;padding-left: 15px;">{{ checkInfo.payeeBank }}</td>
          </tr>
          <tr>
            <td style="background-color: #f7fbfe;"></td>
            <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;">凭证号：</td>
            <td style="text-align: left;padding-left: 15px;">10082*******</td>
            <td style="background-color: #f7fbfe;"></td>
            <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;">定时定频：</td>
            <td style="text-align: left;padding-left: 15px;">实时</td>
          </tr>
          <tr>
            <td style="background-color: #f7fbfe;"></td>
            <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;">金额(大写)：</td>
            <td style="text-align: left;padding-left: 15px;">{{ numberToUpperCase(checkInfo.money) }}</td>
            <td style="background-color: #f7fbfe;"></td>
            <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;">金额(小写)：</td>
            <td style="text-align: left;padding-left: 15px;">{{ checkInfo.money }}</td>
          </tr>
          <tr>
            <td style="background-color: #f7fbfe;"></td>
            <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;">用途：</td>
            <td style="text-align: left;padding-left: 15px;">{{ checkInfo.toUse }}</td>
            <td></td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td style="background-color: #f7fbfe;"></td>
            <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;">是否复核通过：</td>
            <td style="text-align: left;padding-left: 15px;"><span class="rev-type">{{ checkInfo.pass?'通过' : '未通过' }}</span></td>
            <td style="background-color: #f7fbfe;"></td>
            <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;">{{ !checkInfo.pass ? '不通过原因：' : '' }}</td>
            <td style="text-align: left;padding-left: 15px;">{{ !checkInfo.pass ? checkInfo.reason: '' }}</td>
          </tr>
          <tr>
            <td style="background-color: #f7fbfe;border-bottom: none"></td>
            <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;border-bottom: none">制单员：</td>
            <td style="text-align: left;padding-left: 15px;border-bottom: none">出纳</td>
            <td style="background-color: #f7fbfe;border-bottom: none"></td>
            <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;border-bottom: none">制单时间：</td>
            <td style="text-align: left;padding-left: 15px;border-bottom: none">20**-**-** **:**</td>
          </tr>
          </tbody>
        </table>
        <p style="padding-left: 100px;margin-top: 30px;font-size: 14px">短信通知收款人：<input type="checkbox" disabled=""></p>
        <p style="padding-left: 100px;margin-top: 30px;font-size: 14px">邮件通知收款人：<input type="checkbox" disabled=""></p>
        <p style="padding-left: 100px;margin-top: 30px;font-size: 14px">定时定频设置：<input type="checkbox" disabled=""></p>
        <div class="mb-20 mt-20">请输入认证信息</div>
        <el-form ref="pwdForm" :model="pwdForm" :rules="rule" label-width="200px" >
          <el-form-item label="交易密码：" prop="pwd">
            <el-input v-model="pwdForm.pwd" style="width:260px;" type="password"></el-input>
          </el-form-item>
          <el-form-item style="margin-top: 20px">
            <el-button size="medium" type="primary" style="background-color: #1f93d5;border-color: #1f93d5;" @click="lastStep">上一步</el-button>
            <el-button size="medium" type="primary" style="background-color: #1f93d5;border-color: #1f93d5;" @click="confirm">确认</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div v-if="active === 3">
        <img src="../../assets/shenhebutongguo.png" alt="">
        <div class="text-center mt-15">
          <el-button size="medium" type="primary" style="background-color: #1f93d5;border-color: #1f93d5;" @click="active = 1">返回</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PayCheck',
  data () {
    return {
      active: 1,
      reason: '',
      totalNum: 0,
      totalMoney: 0,
      passNum: 0,
      passMoney: 0,
      noPassNum: 0,
      noPassMoney: 0,
      pass: 1,
      pwdForm: {
        pwd: ''
      },
      rule: {
        pwd: [{ required: true, message: '请输入密码', trigger: 'change' }]
      }
    }
  },
  computed: {
    payTransferInfo () {
      return this.$store.state.payTransferInfo
    },
    companyInfo () {
      return this.$store.state.companyInfo
    },
    checkInfo () {
      return this.$store.state.checkInfo
    },
    role () {
      return this.$store.state.role
    },
    userInfo () {
      return this.$store.state.userInfo
    }
  },
  created () {
    this.initData()
  },
  methods: {
    initData () {
      if (parseInt(this.role) === 2) {
        this.calStatistic()
      }
    },
    // 上一步
    lastStep () {
      this.active -= 1
    },
    // 下一步
    check () {
      // 判断有无审核数据
      if (parseInt(this.passNum) === 0 && parseInt(this.noPassNum) === 0) {
        this.$message.warning('请选择审核信息')
        return false
      }
      this.active = 2
    },
    // 确认
    confirm () {
      this.$refs.pwdForm.validate(valid => {
        if (valid) {
          // 验证密码
          if (this.userInfo.password !== this.pwdForm.pwd) {
            this.$message.warning('密码输入错误')
            return false
          }
          this.$store.commit('saveCheckResultInfo', this.checkInfo)
          this.$store.commit('clearCheckInfo')
          this.active = 3
        }
      })
    },
    // 选择结果
    checkColumn (row) {
      // 操作复核数据
      if (row.pass) {
        this.pass = 1
        this.reason = ''
      } else {
        this.pass = 0
      }
      // 将其他行的pass置为空
      if (this.payTransferInfo.length > 0) {
        this.payTransferInfo.forEach(v => {
          if (v.created_at !== row.created_at) {
            this.$set(v, 'pass', '')
          }
        })
      }
      const data = Object.assign(row, { reason: this.reason })
      this.$store.commit('saveCheckInfo', data)
      // 计算统计数据
      this.calStatistic()
    },
    // 审核不通过的原因变化时
    inputChange () {
      const data = { ...this.checkInfo }
      data.reason = this.reason
      this.$store.commit('saveCheckInfo', data)
    },
    calStatistic () {
      this.resetStatistic()
      if (this.payTransferInfo.length > 0) {
        this.totalNum = this.payTransferInfo.length
        this.payTransferInfo.forEach(v => {
          this.totalMoney += v.money ? parseFloat(v.money) : 0
          switch (parseInt(v.pass)) {
            case 0:
              this.noPassNum += 1
              this.noPassMoney += v.money ? parseFloat(v.money) : 0
              break
            case 1:
              this.passNum += 1
              this.passMoney += v.money ? parseFloat(v.money) : 0
              break
          }
        })
      }
    },
    // 初始化统计数据
    resetStatistic () {
      this.totalNum = 0
      this.totalMoney = 0
      this.passNum = 0
      this.passMoney = 0
      this.noPassNum = 0
      this.noPassMoney = 0
    },
    /*
    * 数字转大写
    * */
    numberToUpperCase (n) {
      if (!/^(0|[1-9]\d*)(\.\d+)?$/.test(n)) {
        return '数据非法' // 判断数据是否大于0
      }
      let unit = '千百拾亿千百拾万千百拾元角分'
      let str = ''
      n += '00'
      const indexpoint = n.indexOf('.') // 如果是小数，截取小数点前面的位数
      if (indexpoint >= 0) {
        n = n.substring(0, indexpoint) + n.substr(indexpoint + 1, 2) // 若为小数，截取需要使用的unit单位
      }
      unit = unit.substr(unit.length - n.length) // 若为整数，截取需要使用的unit单位
      for (let i = 0; i < n.length; i++) {
        str += '零壹贰叁肆伍陆柒捌玖'.charAt(n.charAt(i)) + unit.charAt(i) // 遍历转化为大写的数字
      }
      return str.replace(/零(千|百|拾|角)/g, '零').replace(/(零)+/g, '零').replace(/零(万|亿|元)/g, '$1').replace(/(亿)万|壹(拾)/g, '$1$2').replace(/^元零?|零分/g, '').replace(/元$/g, '元整') // 替换掉数字里面的零字符，得到结果
    }
  }
}
</script>

<style lang="scss" scoped>
  .PayCheck {
    .mr-20 {
      margin-right: 20px;
    }
    .header {
      margin: 10px 0;
      padding: 10px;
      font-size: 14px;
      border: solid 1px #dfe1f2;
      background-color: #f4f5fb;
      /deep/ .el-breadcrumb__inner{
        font-size: 16px;
      }
    }
    .pay-transfer-header {
      background-color: #f8f8f8;
      padding: 5px 10px;
    }
    .bottom {
      height: 150px;
      background-color: #f7f7f4;
      border-left: 1px #ccccc9 solid;
      border-bottom: 1px #ccccc9 solid;
      border-right: 1px #ccccc9 solid;
      font-size: 14px;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      padding: 0 10px;
    }
    .reviewContent-table {
      border: 1px #62a7e3 solid;
      width: 100%;
      border-spacing: unset;
      tr {
        height: 35px;
        td {
          height: 40px;
          font-size: 15px;
          text-align: center;
          border-bottom: 1px #c5d7e8 solid
        }
      }
      .rightBottomLine {
        border-bottom: 1px #c5d7e8 solid;
        border-right: 1px #c5d7e8 solid;
      }
    }
  }
</style>
